import React, { Fragment, useEffect, useState } from 'react';
import { useForm, Controller } from 'react-hook-form';
import { Row, Col, Input, Collapse, Button, notification } from 'antd';
import FormControl from 'components/UI/FormControl/FormControl';
import { FormTitle, CollectionSelector } from './AccountSettings.style';
import Loader from 'components/Loader/Loader';
import AxiosFetch from 'library/hooks/AxiosFetch';

const { Panel } = Collapse;

const AdminAirDrop = (props) => {  
  const {metamaskConnected, account} = props;

  const [loading, setLoading] = useState(false);
  const [collections, setCollections] = useState([]);
  const [collectionID, setCollectionID] = useState(1);

  const fetchData = async () => {
    setLoading(true);
    const url = '/getAllCollectionsRoughInfo';    
    const res = await AxiosFetch(url);
    
    if (res.data.success) { 
      setCollections(res.data.data);
    }
    setLoading(false);
  }

  useEffect(fetchData, []);

  const {
    control,
    formState: { errors },
    handleSubmit,
  } = useForm();

  const openNotificationWithIcon = (type, title, description) => {
    notification[type]({
      message: title,
      description: description,
      placement: 'bottomRight'
    });
  };

  const onSubmit = async (submitData) => {
    if(!metamaskConnected)
    {
      openNotificationWithIcon('error', 'Error!', "You need to connect metamask!");
      return;
    }
    setLoading(true);
    let url = '/doAirDrop';
    let airdrop_data = {
      collection_id: collectionID,
      address: submitData.address,
      count: parseInt(submitData.count)
    }
    const result = await AxiosFetch(url, airdrop_data, "POST");
    if(result.data.success) {
      openNotificationWithIcon('success', 'Success!', "Airdrop has been done successfully!");
    }
    else {
      openNotificationWithIcon('error', 'Error!', result.data.message);
    }
    setLoading(false);
  }

  const onCollectionChange = (e) => {
    setCollectionID(parseInt(e.target.value));
  }

  return (
    <Fragment>
      <FormTitle>NFT Airdrop</FormTitle>
      
      {loading &&
        <Loader />
      }
      {!loading &&
        <Collapse defaultActiveKey={['1']}>
          <Panel header="NFT Price Setting" key="1">
            <form className="form-container" onSubmit={handleSubmit(onSubmit)}>
              <CollectionSelector>
                Collection &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <select onChange={onCollectionChange}>
                  {collections.map((item) => {
                    return (
                        <option value={item.id} key={item.id}>{item.name}</option> 
                      );
                    }
                  )}
                </select>
              </CollectionSelector>            
              <Row gutter={30}>
                <Col lg={24}>
                  <FormControl
                    label="Address"
                    htmlFor="address"
                    error={errors.address && <span>This field is required!</span>}
                  >
                    <Controller
                      name="address"
                      control={control}
                      rules={{ required: true }}
                      render={({ field: { onChange, onBlur, value } }) => (
                        <Input onChange={onChange} onBlur={onBlur} value={ value } type="text" />
                      )}
                    />
                  </FormControl>
                </Col>
              </Row>

              <Row gutter={30}>
                <Col lg={24}>
                  <FormControl
                    label="How Many"
                    htmlFor="count"
                    error={errors.count && <span>This field is required!</span>}
                  >
                    <Controller
                      name="count"
                      control={control}
                      rules={{ required: true }}
                      render={({ field: { onChange, onBlur, value } }) => (
                        <Input onChange={onChange} onBlur={onBlur} value={ value } type="number" />
                      )}
                    />
                  </FormControl>
                </Col>
              </Row>

              <div className="submit-container">
                <Button htmlType="submit" type="primary">
                  Airdrop
                </Button>
              </div>
            </form>        
          </Panel>            
        </Collapse>
      }      
    </Fragment>
  );
}

export default AdminAirDrop;